.cutterimage{
    width: 100%;
    height: 570px;
    margin-bottom: 50px;
}

.cutterimage-item{
    text-align: left;
    position: absolute;
    color: white;
    line-height: 1.55;
    font-weight: 900;
}

@media (min-width: 1400px) {
    .cutterimage-item-1 {
        top: 445px;
        right: 5%;
        font-size: 65px;
    }

    .cutterimage-item-3 {
        top: 602px;
        right: 5%;
        font-size: 65px;
    }

    .cutterimage-item-2 {
        top: 183px;
        right: 5%;
        font-size: 65px;
        text-align: right;
    }
}

@media (max-width: 1100px)  {
    .cutterimage-item-1 {
        top: 340px;
        right: 5%;
        font-size: 50px;
    }

    .cutterimage-item-3 {
        top: 470px;
        right: 5%;
        font-size: 50px;
    }

    .cutterimage-item-2 {
        top: 127px;
        right: 5%;
        text-align: right;
        font-size: 50px;
    }
}

@media (max-width: 1400px)  and (min-width: 1100px)  {
    .cutterimage-item-1 {
        top: 430px;
        right: 5%;
        font-size: 50px;
    }

    .cutterimage-item-3 {
        top: 580px;
        right: 5%;
        font-size: 50px;
    }

    .cutterimage-item-2 {
        top: 205px;
        right: 5%;
        text-align: right;
        font-size: 50px;
    }
}

@media (max-width: 800px) {
    .cutterimage-div-mobile{
        height: 50px;
    }
    .cutterimage-div{
        display: none;
    }
}

