.imagecarousel-museum{
    background: #ebebeb;
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.imagecarousel-indicator{
    border: 2px solid white; 
    display: inline-block;
    margin: 8px;
    cursor: pointer;
}

.imagecarousel-indicator-active{
   border: 2px solid white;
    display: inline-block;
    margin: 8px;
    background: white;
}

@media (min-width: 1100px) {

    .imagecarousel-indicator{
        width: 10px;
        height: 10px;
        border-radius: 10px;
    }

    .imagecarousel-indicator-active{
        width: 10px;
        height: 10px;
        border-radius: 10px;
    }

    .arrow:hover {
        background: #416BB3;
        cursor: pointer;
    }

    .arrow {
        width: 30px;
        height: 30px;
        position: absolute;
        background: #6e6e6e;
        border: 1px solid transparent; 
        border-radius: 30px;
    }

    .right {
        right: calc((100% - 870px)/2);
    }

    .left {
        left: calc((100% - 870px)/2);
    }

    .carousel-indicators{
        width: 800px;
        height: 500px;
        position: absolute;
        left: calc((100% - 800px)/2);
        display: flex;
        align-items: flex-end;
        justify-content: center;
    }

    .imagecarousel-museum{
        margin-top: 50px;
        margin-bottom: 50px;
        height: 600px;
    }

}

.imagecarousel-museum-carousel{
    object-fit: contain;
    width: 100%;
    height: 100%;
}

@media (max-width: 1100px) and (min-width: 500px) {


    .imagecarousel-indicator{
        width: 8px;
        height: 8px;
        border-radius: 8px;
    }

    .imagecarousel-indicator-active{
        width: 8px;
        height: 8px;
        border-radius: 8px;
    }

    .arrow {
        width: 25px;
        height: 25px;
        position: absolute;
        background: #6e6e6e;
        border: 1px solid transparent; 
        border-radius: 30px;
    }

    .right {
        right: 15px;
    }

    .left {
        left: 15px;
    }

    .carousel-indicators{
        width: calc(100% - 50px);
        height: 250px;
        position: absolute;
        left: 25px;
        display: flex;
        align-items: flex-end;
        justify-content: center;
    }

    .imagecarousel-museum{
        margin-top: 30px;
        margin-bottom: 30px;
        height: 300px;
    }
}

@media (max-width: 500px) {

    .imagecarousel-indicator{
        width: 6px;
        height: 6px;
        border-radius: 6px;
    }

    .imagecarousel-indicator-active{
        width: 6px;
        height: 6px;
        border-radius: 6px;
    }

    .arrow {
